import "core-js/modules/es.array.push.js";
import { reactive, toRefs, computed } from 'vue';
import { getdata } from '@/utils/request';
import { useRouter } from 'vue-router';
export default {
  name: 'logincontents',
  setup() {
    const data = reactive({
      showyanjing: false,
      zhanghaoinpt: '',
      passwordinpt: '',
      prompttitle: '',
      showprompt: false,
      chenggong: false
    });
    const router = useRouter();
    const showclose = computed(() => {
      if (data.zhanghaoinpt === '') {
        return false;
      } else {
        return true;
      }
    });
    const showopcity = computed(() => {
      if (data.zhanghaoinpt !== '' && data.passwordinpt !== '') {
        return true;
      } else {
        return false;
      }
    });
    const clearinpt = () => {
      data.zhanghaoinpt = data.passwordinpt = '';
    };
    const changeyanjing = () => {
      data.showyanjing = !data.showyanjing;
    };
    const login = () => {
      if (data.zhanghaoinpt === '' || data.passwordinpt === '' || data.zhanghaoinpt === '' && data.passwordinpt === '') {
        return false;
      }
      getdata('/api/mock.json').then(res => {
        const phone = /^1[3456789]\d{9}$/;
        if (!phone.test(data.zhanghaoinpt) && data.passwordinpt.length < 6 || !phone.test(data.zhanghaoinpt) && data.passwordinpt.length >= 6) {
          data.showprompt = true;
          data.prompttitle = '请填写正确的手机号码';
          setTimeout(() => {
            data.showprompt = false;
          }, 2000);
        } else if (data.passwordinpt.length < 6 && phone.test(data.zhanghaoinpt)) {
          data.showprompt = true;
          data.prompttitle = '请输入6至25位密码';
          setTimeout(() => {
            data.showprompt = false;
          }, 2000);
        } else if (data.passwordinpt.length >= 6 && phone.test(data.zhanghaoinpt) && data.zhanghaoinpt !== res.users[0].phone) {
          data.showprompt = true;
          data.prompttitle = '账号不存在';
          setTimeout(() => {
            data.showprompt = false;
          }, 2000);
        } else if (data.zhanghaoinpt === res.users[0].phone && data.passwordinpt.length >= 6 && data.passwordinpt !== res.users[0].password) {
          data.showprompt = true;
          data.prompttitle = '该账号或密码有误，如有问题请联系客服';
          setTimeout(() => {
            data.showprompt = false;
          }, 2000);
        } else {
          data.chenggong = true;
          setTimeout(() => {
            setTimeout(() => {
              router.push('/');
            }, 100);
            const token = res.users[0].id + res.users[0].phone;
            localStorage.setItem('token', JSON.stringify(token));
            data.chenggong = false;
          }, 1000);
        }
      });
    };
    return {
      ...toRefs(data),
      showclose,
      showopcity,
      clearinpt,
      changeyanjing,
      login
    };
  }
};